import React from "react";
import Select, {components} from "react-select";

function ReactSelect({ selectedOption, handleChange, options, title, numberOfItemsToShow = 3, showCheckbox = false, showNumberSelected = false, ...rest }) {

    const ValueContainer = ({ children, getValue, ...props }) => {
      console.log(children, getValue, props)
      let maxToShow = numberOfItemsToShow;
      var length = getValue().length;
      let displayChips = React.Children.toArray(children).slice(0, maxToShow);
      let shouldBadgeShow = length > maxToShow;
      let displayLength = length - maxToShow;
    
      return (
        <components.ValueContainer {...props}>
          {!props.selectProps.inputValue && displayChips}
          <div className="more_item">
            {shouldBadgeShow &&
              `+ ${displayLength} item${length != 1 ? "s" : ""}`}
          </div>
        </components.ValueContainer>
      );
    };

    const Option = (props) => {
      return (
        <div>
          <components.Option {...props}>
            <div className="checkbox">
              <input
                type="checkbox"
                checked={props.isSelected}
                onChange={() => null}
              />{" "}
              <label>{props.label}</label>
            </div>
          </components.Option>
        </div>
      );
    };

    let componentsToRender = {
      ...(showNumberSelected && {ValueContainer}),
      ...(showCheckbox && {Option})
    }

    return (
        <>
            <Select
                value={selectedOption}
                onChange={handleChange}
                options={options}
                isClearable={false}
                placeholder={title}
                classNamePrefix="react-prefix"
                className={`react-select ${selectedOption?.color ? selectedOption?.color : ""}`}
                isSearchable={false}
                components={{ ...componentsToRender }}
                {...rest}
            //menuIsOpen={true}
            // styles={customStyles}
            />
        </>
    );
}

export default ReactSelect;
