import React, { useEffect, useState } from "react";
import "./style.scss";
import { INPUT_TYPES, LABEL, days, initialDaysData } from "shared/Constants";
import { appendSeconds } from "shared/Utilities";
import TimePicker from 'rc-time-picker';
import moment from "moment"

let labels = {
  opening_time: "Opening Time",
  closing_time: "Closing Time",
  lunch_start: "Lunch Start",
  lunch_end: "Lunch End"
};

const JobTiming = ({ data = [], setJobTimingData, handleSetIsSelectedAll, isSelectAll, disabled = false, enabledDays = [], handleDisabledHours = () => {}, handleDisabledMinutes = () => {} }) => {

  const [selectedDay, setSelectedDay] = useState(0);
  const [selectAll, setSelectAll] = useState(isSelectAll ? true : false);
  const [daysSelected, setDaysSelected] = useState(Array(7).fill(true))
  const [timings, setTimings] = useState({
    opening_time: "",
    closing_time: "",
    lunch_start: "",
    lunch_end: ""
  });
  const [dayData, setDayData] = useState(initialDaysData);
  
  // to re-render component to show isselectall checkbox
  useEffect(() => {
      setSelectAll(isSelectAll);
  },[isSelectAll])

  const handleSelectDay = (index) => {
    setSelectedDay(index);
    let filteredDay = {};
      filteredDay =
        dayData?.find((dayDetails) => dayDetails?.day === index) || {};
    setTimings({
      opening_time: filteredDay.opening_time || "",
      closing_time: filteredDay.closing_time || "",
      lunch_start: filteredDay.lunch_start || "",
      lunch_end: filteredDay.lunch_end || ""
    });
  };

  const handleSelectAll = (event) => {
    if (event.target.checked) {
      Object.keys(days).forEach((item, key) => {
        updateJobTimingData(
          key,
          timings.closing_time,
          timings.opening_time,
          timings.lunch_start,
          timings.lunch_end
        );
      });
      setDaysSelected(Array(7).fill(true))
    } else {
      setDaysSelected(Array(7).fill(false));
      Object.keys(days).forEach((item, idx) => {
        updateJobTimingData(
          idx,
          "",
          "",
          "",
          ""
        );
      })
      setTimings({
        opening_time: "",
        closing_time: "",
        lunch_start: "",
        lunch_end: ""
      });
    }
    handleSetIsSelectedAll(!selectAll);
    setSelectAll(!selectAll);
  };

  const handleTimeChange = (event, field) => {
    setTimings({ ...timings, [field]: moment(event).format("HH:mm:ss") });
  };

  const onCheckboxSelect = (e, index) => {
    if (!e.target.checked) {
      let newState = [...dayData];
      newState.splice(index, 1, {
        ...newState[index],
        closing_time: '',
        opening_time: '',
        lunch_start: '',
        lunch_end: '',
      });
      let filteredDay =
      newState?.find((dayDetails) => dayDetails?.day === index) || {};
      if(selectedDay == index){
        setTimings({
          opening_time: filteredDay.opening_time || "",
          closing_time: filteredDay.closing_time || "",
          lunch_start: filteredDay.lunch_start || "",
          lunch_end: filteredDay.lunch_end || ""
        });
      }
      setDayData([...newState]);
      //Setting selected All to false will
      setSelectAll(false)
      handleSetIsSelectedAll(false)
    }
    let data = [...daysSelected];
    data.splice(index, 1, e.target.checked);
    // if(data.every((item)=>item)){
    //   setSelectAll(true)
    //   handleSetIsSelectedAll(true)
    // }
    setDaysSelected([...data]);
  };

  function updateJobTimingData(
    day,
    closing_time,
    opening_time,
    lunch_start,
    lunch_end
  ) {
    setDayData((prevJobTimingData) => {
      const updatedData = prevJobTimingData.map((data) => {
        if (data.day === day) {
          return {
            ...data,
            closing_time,
            opening_time,
            lunch_start,
            lunch_end
          };
        }
        return data;
      });
      return updatedData;
    });
  }

  const isDisabled = (field) => {
    console.log(field, "field")
    if(!daysSelected[selectedDay]){
      return true
    }
    if(field == "closing_time" || field == "opening_time"){
      if(field === "closing_time" && !timings["opening_time"]){
        return true
      }else if(field === "closing_time" && timings["opening_time"]){
        return false
      }
    }
    if(field == "lunch_end" || field == "lunch_start"){
      if(!timings["opening_time"] || !timings["closing_time"]){
        return true
      }
      if(field === "lunch_end" && !timings["lunch_start"]){
        return true
      }else if(field === "lunch_end" && timings["lunch_start"]){
        return false
      }
    }
  }

  const minValue = (field) => {
    if(field == "closing_time"){
      return timings["opening_time"]
    }
    if(field == "lunch_end"){
      return timings["lunch_start"]
    }
  }

  useEffect(() => {
      //changing parent state on change of internal state
      let updatedData = [];
      dayData.forEach((item, idx) => {
        updatedData.push({
          ...item,
          isSelected: daysSelected[idx]
        })
      })
      setJobTimingData([...updatedData])
  }, [dayData, daysSelected])

  useEffect(() => {
    if (data?.length) {
      let newData = [...dayData];
      data.forEach(item => {
        newData.splice(item.day, 1, item)
      })
      setDayData([...newData]);
      let filteredDay =
        data?.find((dayDetails) => dayDetails?.day === selectedDay) || {};
      setTimings({
        opening_time: filteredDay.opening_time || "",
        closing_time: filteredDay.closing_time || "",
        lunch_start: filteredDay.lunch_start || "",
        lunch_end: filteredDay.lunch_end || ""
      });
      console.log(data, "Data insdie4")
      //Checking which fields are checked
      let isSelectedData = Array(7).fill(false)
      data.forEach(item => {
        if(item?.closing_time || item?.opening_time || item?.lunch_end || item?.lunch_start){
          isSelectedData.splice(item.day, 1, true)
        }
      })
      console.log(isSelectedData, "Data insdie4")
      setDaysSelected(isSelectedData)
    }
  }, [data])

  useEffect(() => {
    if(selectAll){
      dayData.forEach((item, idx) => {
        updateJobTimingData(
          idx,
          timings.closing_time,
          timings.opening_time,
          timings.lunch_start,
          timings.lunch_end
        );
      })
    }else{
      updateJobTimingData(
        selectedDay,
        timings.closing_time,
        timings.opening_time,
        timings.lunch_start,
        timings.lunch_end
      );
    }
  }, [timings]);

  return (
    <>
      <div className="mb-2">
        <h6 className="label_title mb-4">
          <span>{LABEL.JOB_TIMINGS}</span>
        </h6>
        <div className="d-flex flex-wrap job_btn">
          {Object.values(days).map((day, index) => (
            <div className={`btn btn-md me-2 mb-2 ${
              selectedDay === index ? 'btn-outline-primary selected' : ''
            }`}>
            <div className="checkbox">
                <input
                  id={index}
                  checked={daysSelected[index]}
                  type={INPUT_TYPES.CHECKBOX}
                  onChange={(e) => {
                    onCheckboxSelect(e, index);
                  }}
                  disabled={disabled || !enabledDays.includes(index)}
                />
                <label> </label>
              </div>
            <span
              key={index}
              onClick={() => handleSelectDay(index)}
            >            
                {day}
            </span>
            </div>
          ))}
        </div>
      </div>

      <div className="card mb-4">
        <div className="card-body bg-white">
          <div className="row">
            {Object.keys(labels).map((field, index) => (
              <div className="col-md-3 my-2" key={index}>
                <label htmlFor={field}>{`${labels[field]}`}</label>
                {/* <input
                  type="time"
                  id={field}
                  value={timings[field]}
                  className="form-control"
                  onChange={(event) => handleTimeChange(event, field)}
                  disabled={disabled || isDisabled(field)}
                  min={minValue(field)}
                /> */}
                <TimePicker
                  use12Hours={true}
                  showSecond={false}
                  value={timings[field] ? moment(timings[field], "HH:mm:ss") : ""}
                  onChange={(event) => handleTimeChange(event, field)}
                  disabledHours={() => handleDisabledHours(field, selectedDay)}
                  disabledMinutes={() => handleDisabledMinutes(field, selectedDay)}
                  disabled={disabled || isDisabled(field)}
                  allowEmpty={false} 
                />
              </div>
            ))}
          </div>
        </div>
      </div>

      <div className="checkbox mb-4">
        <input
          type="checkbox"
          id="selectAll"
          checked={selectAll}
          onChange={handleSelectAll}
          disabled={disabled}
        />
        <label htmlFor="selectAll">Select all for same day</label>
      </div>
    </>
  );
};

export default JobTiming;
