// libs
import { useState, useEffect } from "react";
import { Route, Switch, useHistory } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";

// widgets
import AdminSidebar from "../Sidebar";
import Header from "../Header";
import LogoutModal from "components/LogoutModal";
import NotFound from "views/NotFound";

// assets
import { Images } from "shared/Images";

// consts
import { ADMIN_ROUTES } from "routes/AdminRoutes";
import { LABEL, ROUTES, SNACK_MESSAGES, SNACK_VARIANT } from "shared/Constants";

// utils
import { getDocsUrl } from "shared/Utilities";

// hooks
import useNotification from "hooks/Notifications";

// styles
import "../styles.scss";

// actions
import { logout } from "redux/Actions/Auth";

const AdminLayout = ({ children, is404, isAdmin, isPharmacyUser = false }) => {
  const dispatch = useDispatch();
  const history = useHistory();
  const profile = useSelector((state) => state.common.profileData);
  const [msg, sendNotification] = useNotification();
  const [showLogout, setShowLogout] = useState(false);
  const [sidebarToggle, setSidebarToggle] = useState(true);

  // add class to the body on admin routes as per styles constraint
  useEffect(() => {
    document.body.classList.add("admin_panel");

    return () => {
      document.body.classList.remove("admin_panel");
    }
  }, []);

  const profileData = [
    {
      title: LABEL.MY_PROFILE,
      image: getDocsUrl(profile?.owner_info?.photo) || Images.DefaultPic,
      onClick: () => {
        history.push(ROUTES.PROFILE);
      },
    },
    {
      title: LABEL.LOGOUT,
      image: Images.logout,
      onClick: () => setShowLogout(true),
    },
  ];

  const onLogout = () => {
    dispatch(
      logout(
        "",
        (res) => {
          setShowLogout(false);
          sendNotification({
            msg: res?.message || SNACK_MESSAGES.SAVED_SUCCESSFULLY,
            variant: SNACK_VARIANT.SUCCESS,
          });
          history.push(ROUTES.HOMEPAGE);
        },
        (err) => {
          sendNotification({
            msg: err?.message || SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
            variant: SNACK_VARIANT.ERROR,
          });
        }
      )
    );
  };

  return (<>
    <main className="main slide-active">
      <AdminSidebar
        sidebarToggle={sidebarToggle}
        setSidebarToggle={setSidebarToggle}
      />
      <div className="content">
        <Header
          setShowLogout={setShowLogout}
          showLogout={showLogout}
          profileData={profileData}
          sidebarToggle={sidebarToggle}
          setSidebarToggle={setSidebarToggle}
        />
        <div className="container-fluid px-3 px-md-4 pb-3">
          <Switch>
            {isPharmacyUser && ADMIN_ROUTES.map((route, routeIdx) => (
              <Route
                path={route.path}
                key={routeIdx}
                component={route.component}
                exact={route.exact}
              />
            ))}
            {is404 && <Route path="*" render={() => <NotFound isAdmin={isAdmin}/>}/>}
          </Switch>
        </div>
      </div>
    </main>
    {showLogout && <LogoutModal
      onLogout={onLogout}
      showLogout={showLogout}
      setShowLogout={setShowLogout}
    />}
  </>
  );
};

export default AdminLayout;
