import { BrowserRouter, Redirect, Route, Switch, matchPath, useLocation } from "react-router-dom";

import { AUTH_ROUTES } from "./AuthRoutes";
import AppLayout from "components/Core/AppLayout";
import DocumentTitle from "./DocumentTitle";
import { PRIVATE_ROUTES } from "./PrivateRoutes";
import { PUBLIC_ROUTES } from "./PublicRoutes";
import PrivateLayout from "components/Core/PrivateLayout";
import PublicLayout from "components/Core/PublicLayout";
import React, { useEffect, useMemo } from "react";
import RenderRoutes from "./RenderRoutes";
import { updateAuthToken } from "shared/Axios";
import { useDispatch, useSelector } from "react-redux";
import CustomLoader from "components/CustomLoader";
import { setAuthToken } from "redux/Actions/Auth";
import Header from "components/Header";
import { HEADER_DATA } from "views/HomePage/constants";
import AdminLayout from "components/Core/Admin/Layout";
import { ADMIN_ROUTES, ADMIN_BASE_ROUTE, ROUTES } from "shared/Constants";
import NotFound from "views/NotFound";
const DEFAULT_AUTHENTICATED_ROUTE = "/";
const DEFAULT_GUEST_ROUTE = "/";

const GuestRoutes = () => {
    return (
        <>  
            <PublicLayout>
                <Header headerList={HEADER_DATA} />
                <RenderRoutes routes={[...AUTH_ROUTES, ...PUBLIC_ROUTES]} />
            </PublicLayout>
            <Redirect from="*" to={ROUTES.HOMEPAGE} />
        </>
    );
};

const AuthenticatedRoutes = () => {
    const profile = useSelector((state) => state.common.profileData) || {};
    const location = useLocation();
    const isPharmacyUser = profile?.user_type == 1;;
    const routes = PUBLIC_ROUTES.concat(PRIVATE_ROUTES);
    const existingRoutes = [...Object.values(isPharmacyUser ? ADMIN_ROUTES : {}), ...Object.values(ROUTES)];
    const isAdmin = useMemo(() => location.pathname?.includes(ADMIN_BASE_ROUTE), [location?.pathname]);
    const is404 = useMemo(() => !existingRoutes?.some((route) => matchPath(location?.pathname, {
        path: route,
    })?.url === location?.pathname), [location?.pathname]);
    return (
        <>
            <PrivateLayout isAdmin={isAdmin}>
                <RenderRoutes routes={routes} is404={is404} isAdmin={isAdmin}/>
            </PrivateLayout>
            {/* show admin routes to pharmacy user only as of now i.e. userType = 1*/}
            <Route path={ADMIN_BASE_ROUTE}>
                <AdminLayout is404={is404} isAdmin={isAdmin} isPharmacyUser={isPharmacyUser}/>
            </Route>
        </>
    );
};

const RootRouter = () => {
    const dispatch = useDispatch();
    const token = useSelector((state) => state.auth.token);
    const loading = useSelector((state) => state.common.loading);
    updateAuthToken(token);
    const baseName = process.env.REACT_APP_BASE_NAME;
    const isAuthenticated = !!token;
    return (
        <BrowserRouter basename={baseName}>
            <CustomLoader visible={loading}>
                <DocumentTitle isAuthenticated={isAuthenticated} />
                <AppLayout isAuthenticated={isAuthenticated}>
                    {token ? <AuthenticatedRoutes /> : <GuestRoutes />}
                </AppLayout>
            </CustomLoader>
        </BrowserRouter>
    );
};

export default RootRouter;
